.app-wrapper {

    width: 1200px;
    margin: 0 auto;

    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-template-areas: 
    'h h'
    'n c';
}

.app-wrapper-content {
    grid-area: c;
    background-color: khaki;
    margin: 10px 0 0 10px;
}
